import { _decorator, Component, Sprite, tween, Color } from 'cc';
const { ccclass, property } = _decorator;

enum PopupState {
    None,
    Hidden,
    Showing,
    Shown,
    Hiding,
}

@ccclass('FadePopup')
export class FadePopup extends Component {
    @property(Sprite)
    targetSprite: Sprite | null = null;

    @property
    showDuration: number = 0.5;

    @property
    hideDuration: number = 0.5;

    @property
    isOpenAtStart: boolean = false;

    private state: PopupState = PopupState.None;

    start() {
        this.initOpacity();
    }

    initOpacity() {
        if (this.targetSprite) {
            if (this.isOpenAtStart) {
                this.targetSprite.color = new Color(this.targetSprite.color.r, this.targetSprite.color.g, this.targetSprite.color.b, 255);
                this.state = PopupState.Shown;
            } else {
                this.targetSprite.color = new Color(this.targetSprite.color.r, this.targetSprite.color.g, this.targetSprite.color.b, 0);
                this.state = PopupState.Hidden;
            }
        }
    }

    show() {
        if (this.state !== PopupState.Hidden && this.state !== PopupState.Hiding && this.state !== PopupState.None) {
            return;
        }
        this.state = PopupState.Showing;

        if (this.targetSprite) {
            tween(this.targetSprite.color)
                .to(this.showDuration, { a: 255 }, { easing: 'sineOut' })
                .call(() => {
                    this.state = PopupState.Shown;
                })
                .start();
        }
    }

    hide() {
        if (this.state !== PopupState.Shown && this.state !== PopupState.Showing) {
            return;
        }
        this.state = PopupState.Hiding;

        if (this.targetSprite) {
            tween(this.targetSprite.color)
                .to(this.hideDuration, { a: 0 }, { easing: 'sineIn' })
                .call(() => {
                    this.state = PopupState.Hidden;
                })
                .start();
        }
    }
}
